import React from 'react';
import Profile from './Profile';
import TabBar from './TabBar';

class App extends React.Component {
    render() {
        return (
            <div className="ui vertical segments">
                <div className="ui segment">
                    <TabBar />
                </div>
                <div className="ui segment">
                    <Profile />
                </div>
            </div>
        );
    }
};

export default App;